import React from 'react';
import _get from 'lodash.get';
import { gql, useMutation, useQuery } from '@apollo/client';
import { Formik, FormikHelpers } from 'formik';
import { generatePath, useHistory } from 'react-router-dom';
import MainCard from '../../../ui-component/cards/MainCard';
import graphqlSchema from '../../../graphql';
import Loader from '../../../ui-component/Loader';
import Profile from '../../application/user/components/Profile';
import { EditUserProfile } from '../../application/user/Validatation';
import { TEditUserProfile } from '../../../types';
import { useSnackBar } from '../../../hooks';
import { PATHS } from '../../../routes/path';

const ProfilePage = () => {
	const showSnackBar = useSnackBar();
	const history = useHistory();

	const {
		loading: loadingUserGroups,
		error: userGroupsError,
		data: userGroupsData,
	} = useQuery(gql(graphqlSchema.users_UserGroups));
	const [doCreateNewUser] = useMutation(gql(graphqlSchema.users_CreateUser));
	const groups = _get(userGroupsData, 'users_UserGroups');

	const initialValues = {
		firstName: '',
		lastName: '',
		email: '',
		phone: '',
		userGroup: '',
	};

	if (loadingUserGroups) return <Loader />;

	if (userGroupsError) {
		const message = _get(userGroupsError, 'message', 'Something went wrong');
		showSnackBar({
			message,
		});
		return <></>;
	}

	const onSubmit = async (
		values: TEditUserProfile,
		{ setStatus, setSubmitting }: FormikHelpers<TEditUserProfile>
	) => {
		try {
			const { email, firstName, lastName, phone, userGroup } = values;
			const variables = {
				Email: email,
				FirstName: firstName,
				LastName: lastName,
				Phone: phone || undefined,
				UserGroup: userGroup,
			};
			const { data } = await doCreateNewUser({
				variables,
			});
			showSnackBar({
				message: 'User has created',
				alertSeverity: 'success',
			});
			const userId = _get(data, ['users_CreateUser', 'UserId']);
			const userLink = generatePath(PATHS.USERS_EDIT, {
				id: userId,
			});
			history.push(userLink);
		} catch (e: any) {
			const message = _get(e, 'message', 'Something went wrong');
			setStatus({ success: false });
			setSubmitting(false);
			showSnackBar({ message });
		}
	};

	return (
		<MainCard title='Add new user' sx={{ mb: 3 }}>
			<Formik
				initialValues={initialValues}
				onSubmit={onSubmit}
				validationSchema={EditUserProfile}
			>
				{({ handleSubmit }) => (
					<form noValidate onSubmit={handleSubmit}>
						<Profile isGroupDisable={false} groups={groups} isAddNew />
					</form>
				)}
			</Formik>
		</MainCard>
	);
};

export default ProfilePage;
